import React from "react"
import components from '../components'


const { Article, Layout, Main, MainWithPagination, Pagination, Sidebar } = components

const IndexPage = ({ pageContext }) => {
  const { group, index, first, last, pageCount } = pageContext
  const previousUrl = index - 1 === 1 ? '' : (index - 1).toString()
  const nextUrl = index + 1 > pageCount ? '' : (index + 1).toString()

  const articlesGrid = group.filter(({ node }) => node.frontmatter.title.length > 0)
    .map(({ node: post }) => {
      return <Article date={post.frontmatter.date} title={post.frontmatter.title} body={post.excerpt} to={post.frontmatter.path} />
    })
  return (
    <Layout>
      <MainWithPagination >
        <Main article={articlesGrid} />
        <Pagination
          prev={previousUrl}
          next={nextUrl}
          total={pageCount}
          isFirst={first}
          isLast={last}
        />
      </MainWithPagination>
      <Sidebar />
    </Layout>
  )
}



export default IndexPage
